import { useEffect, useState } from "react"

export default function Dashboard() {
  const [deployments, setDeployments] = useState([])

  useEffect(() => {
    fetch("http://localhost:3005/api/deployments")
      .then(res => res.json())
      .then(setDeployments)
  }, [])

  return (
    <div style={{ padding: 40 }}>
      <h2>Deployments</h2>
      {deployments.map(d => (
        <div key={d._id}>
          {d.repo} → {d.platform} → {d.status}
        </div>
      ))}
    </div>
  )
}
